body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@media screen and (max-width:1400px){
    .card{
        margin: 50px;
        justify-content: space-around;
        width: 100%;
    }
}
.card{
    width: 300px;
    box-shadow: 2px 0px 5px #888888;
    border-radius: 8px;
    height: auto;
    display: flex;
    flex-direction: column;
    justify-content :space-between;
    
}
.imgcontain{
    height: 200px;
}
.imgcontain>img{
    width: 100%;
    height: auto;
}
.container {
    padding: 2px 16px;
    text-align: center;
    align-items: center;
}
.readMoreBtn{
    background-color: rgb(5, 58, 58);
    width: 150px;
    color: white;
    border-radius: 5px;
    height: 35px;
    width: 100%;
    cursor: pointer;
    font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

@media screen and (max-width:1100px) {
    .containerNews{
        padding-left: 0px;
        padding-right: 0px;
    }
    .row{
        padding-left: 0px;
        padding-right: 0px;
        justify-content: space-between;

    }
}
@media screen and (max-width:995) {
    .containerNews{
        padding-left: 0px;
        padding-right: 0px;
    }
    .row{
        /* display: flex;
        align-items: center;
        justify-content: center;*/
        width:inherit;
        width: 100%;
        margin: 0px;  
    grid-template-columns: repeat(auto-fill, minmax(280px, 3fr));

    }
}
.row{
    display: grid;
    justify-content: space-between;
    place-items:center; 
    place-content:center; 
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    padding-left: 100px;
    padding-right: 100px; 
    overflow:auto;
    grid-gap: 20px;
    align-items: stretch;
}
.containerNews{
    padding-left: 100px;
    padding-right: 100px;
    box-sizing: border-box;

}
.heading{
    text-align: center;
    margin-top: 70px;
}
.spanColorB{
    color: blue;
}
.spanColorR{
    color: rgb(160, 11, 11);
}
.handlePages{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin: 30px;
    height: 30px;
}
.handlePages> button{
    width: 130px;
    background-color: rgb(5, 58, 58);
    color: white;
    font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    border-radius: 8px;
    font-size: 20px;
}
.rootLoader{
    position: absolute;
    left: 0%;
    width: 100%;
    height: 100%;
    background-color: white;
    display: flex;
    justify-content: center;
    align-items: center;
}
.Navcontainer{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-left: 2%;
    padding-right: 2%;
    background-color: rgb(5, 58, 58);
    font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    box-shadow: 5px 10px 18px #888888;
    position: fixed;
    left: 0px;
    top: 0px;
    width: 100%;
    overflow: auto;
    box-sizing: border-box;
}
.login{
    padding: 8px;
    border: 1px solid white;
    border-radius: 5px;
}
.newsLogo{
    display: flex;
    align-items: center;
    width: 50%;
    justify-content: space-between;
}
.login:hover{
    background-color: rgb(13, 102, 102);
}
.newsLogo img{
    width: 50px;
    -webkit-filter: invert(100%);
            filter: invert(100%);
}
a{
    text-decoration: none;
    color: white;
}
a:hover{
    text-decoration: underline;
    color: rgb(245, 245, 188);
    -webkit-transform: scale(1.07);
            transform: scale(1.07);
}
